.robotofont{
    font-family: 'Roboto', sans-serif;
}

.heart{
    position: relative;
    background-color: crimson;
    width: 15px;
    height: 20px;
    border-radius: 200px 200px 0px 0px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.heart::before{
    content: " ";
    position: absolute;
    height: 20px;
    width: 15px;
    background-color: crimson;
    border-radius: 200px 200px 0px 0px;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    top: 5px;
    left: -5px;
}
